@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0;
  padding: 30px;
  background-image: url(https://i.imgur.com/6r8Oi1q.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% auto;
  min-height: 100vh;
}

body, html {
  scrollbar-width: none;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
display: none;
}

.hometitle {
  margin-top: 200px;
  font-size: 60px;
  font-family: 'Montserrat Black', sans-serif;
  color: white;
  text-align: center;
  margin-bottom: 0;
}
nav {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    align-items: center;
}

nav h1 {
    margin: 0;
    padding: 0;
    color: white;
    font-family: 'Montserrat Black', sans-serif;
    font-size: 30px;
}

nav ul {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
    color: white;
}

nav li {
    margin-left: 200px;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    font-size: 30px;
}
h1 {
    color: white;
    text-transform: uppercase;
    font-family: 'Montserrat Black', sans-serif;
    font-size: 50px;
}

.Countries_title {
    margin-bottom: 10px;
}

.Indonesia {
    margin-top: 0%;
    margin-bottom: 0;
    margin-left: 10%;
    margin-right: 10%;
}

.col {
    display: flex;
    justify-content: space-between;
    color: white;
    padding-left: 20px;
    padding-right: 20px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 200;
    font-style: italic;
    background-color: rgb(39, 39, 39);
}

.col_cont {
    flex: 0.15 1;
    text-align: left;
}

.col_col {
    /* text-align: center; */
    flex: 0.15 1;
}

.col_dat {
    /* text-align: center; */
    flex: 0.08 1;
}

.col_det {
    /* text-align: center; */
    flex: 0.1 1;
}

.container {
    margin: 0;
    padding: 0;
}
.province_contents {
    margin: 0;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 0;
    background-color: rgb(27, 27, 27);
    margin-bottom: 10px;
}

.province_contents:hover {
    background-color: grey;
    cursor: pointer;
}

.province_title {
    flex: 0.2 1;
    font-size: 20px;
    margin: 0;
}

.Arrow {
    color: white;
    font-size: 30px;
}

.province_contents p {
    flex: 0.15 1;
    color: white;
    font-size: 15px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
}
.Gobal p {
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    color: white;
    font-style: italic;
    margin-left: 20px;
    margin-right: 20px;
}

.gobal_status {
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
}
.Country_Detail {
    margin: 0;
    padding-left: 15%;
    padding-right: 15%;
    color: white;
}

.title_container {
    margin-top: 40px;
    display: flex;
    justify-content: flex-start;
}

.Back-btn {
    margin: 0;
    padding: 0;
    font-size: 30px;
}

.Back-btn:hover {
    cursor: pointer;
}

.Cty-btn {
    font-size: 30px;
    margin: 0;
    padding: 0;
    margin-left: 15px;
    text-transform: none;
}

.data_container {
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.data_containerSet {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: rgb(36, 36, 36);
    padding-left: 20px;
    padding-right: 20px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    margin-bottom: 3px;
}

.data_containerSet:hover {
    background-color: rgb(73, 73, 73);
}
